<template>
    <div class="box" :class="{
        'left':isLeft,
        'right':!isLeft
    }">
        <p class="text text_time">{{time}}</p>
        <p class="text text_title">{{title}}</p>
        <p class="text text_description">{{description}}</p>

    </div>
</template>
<style lang="scss" scoped>
.box{
    background-color: $schemes-1-text-dark;
    border-radius: .5rem;
    padding: 3%;
    box-shadow: $shadow-section;
    &:not(:last-child){
        margin-bottom: 3%;
    }
}
.left{
    transform: skewX(5deg);
    >*{
        transform: skewX(-5deg);
    }

}
.right{
    transform: skewX(-5deg);
    >*{
        transform: skewX(5deg);
    }
}
.text{
    &_time{
        color:$schemes-1-bg;
        font-weight: 800;
    }
    &_title{
        color: $schemes-1-text-light;
        font-size: 1.5rem;
    }
    &_description{
        // color: $gray-light;
        font-size: .9rem;
        color: #8ee4af;
    }
}
</style>
<script>
export default {
    props:['time','title',"description","index"],
    data(){
        return{
            isLeft:true
        }
    },
    mounted() {
        if(this.index%2!=0){
            this.isLeft=!this.isLeft
        }
    },
}
</script>